import { admin } from './../../services/admin.service';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  loading = false;

  constructor(private http: HttpClient,
    private admin: admin) {
    let statusReq = this.http.get<{ name: string, value: number }[]>(this.admin.baseUrl + '/Admin/StudentStatus', { observe: 'response' })
    let paymentStatusReq = this.http.get<{ name: string, value: number }[]>(this.admin.baseUrl + '/Admin/PaymentStatus', { observe: 'response' })
    let districtsReq = this.http.get<{ name: string, value: number }[]>(this.admin.baseUrl + '/Admin/PlaceOfBirthStatus', { observe: 'response' })
    let historyReq = this.http.get<{ name: string, value: number }[]>(this.admin.baseUrl + '/Admin/NewStudentStatus', { observe: 'response' })
    let siblingsReq = this.http.get<{ name: string, value: number }[]>(this.admin.baseUrl + '/Admin/HasSiblingsStatus', { observe: 'response' })

    let multiCall = forkJoin([statusReq, paymentStatusReq, districtsReq, historyReq, siblingsReq]);

    this.loading = true;
    multiCall.subscribe(
      res => {
        this.loading = false;
        this.statusData = res[0].body;
        this.paymentStatus = res[1].body;
        this.districts = res[2].body;
        this.history = res[3].body;
        this.siblings = res[4].body;
      }, err => {
        this.loading = false;
        console.log("Can't reach end point")
      }
    )
  }

  statusData: {
    name: string,
    value: number
  }[] = []

  paymentStatus: {
    name: string,
    value: number
  }[]
  districts: {
    name: string,
    value: number
  }[]

  history: {
    name: string,
    value: number
  }[]

  siblings: {
    name: string,
    value: number
  }[]
  paymentMethods: {
    name: string,
    value: number
  }[]
  ngOnInit(): void {
  }

}
